:root{
    --maincolor:rgb(48, 48, 48);
    --light:rgb(240, 240, 240);
    --soft: rgb(97, 97, 97);
    overflow: hidden;
}

body{
    margin: 0;
    padding: 0;
    overflow: hidden;
}
.body::-webkit-scrollbar{
    display: none;
}
.login{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 100vh;
    background-color:var(--light);
    justify-content: flex-start;
    align-items: center;
}
.login_heading{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 30vh;
   
}
.login_head{
    font-size: 3em;
    color: rgb(70, 70, 70);
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: 900;
}
.login_box{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10vh;
    background-color: var(--maincolor);
    padding: 5vh;
    border-radius: 10px;
    width: 50%;

}
.login_input{
    width: 100%;
    border: 0;
    background-color: rgb(171, 171, 171);
    border-bottom: solid black 2px;
    font-size: 1.2em;
    font-family:'Times New Roman', Times, serif;
    padding: 1vh;
    border-radius: 10px;
    text-align: center;


}
.login_btn{
    background-color: var(--soft);
    color: white;
    border-radius: 10px;
    font-size: 1.5em;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-weight: 900;
    border: 0;
    padding: 1vh;
}
.name{
    color: aliceblue;
}

.foot{
    display: flex;
    width: 100%;
    justify-content: space-around;
    align-items: center;
    height: 10vh;
    background-color: var(--maincolor);
    bottom: 0px;
    position: fixed;
}
.nav_text{
    text-decoration: none;
    font-family: fantasy;
    font-size: 1.5em;
    color: white;
}

.nav_icon{
    height: 1em;
    width: 1em;
}

.head{
    display: flex;
    width: 100%;
    height: 10vh;
    justify-content: center;
    align-items: center;
    background-color: var(--maincolor);
    color: white;
}

.home_box{
    display: flex;
    padding: 3vh;
    gap: 5vh;
    display: flex;
    flex-direction: column;
}

.home1{
    display: flex;
     /* height: 10vh; */
    /* padding-left: 2vh; */
    width: 100%;
    justify-content: space-between;
}

.home11{
     display: flex;
     height: 25vh;
     width: 53%;
     flex-direction: column;
     justify-content: center;
     align-items: center;
     background-color: var(--maincolor);
     border-radius: 10px;
 }



 .home111{
    padding-top: 8%;
    padding-left: 25%;
    width: 100%;
    color: white;
    display: flex;
    justify-content: flex-start;
 }
 .home112{
    display: flex;
    height: 20vh; 
    justify-content: center;
    align-items: center;   
 }
.home1121{
    display: flex;
    height: 4vh;
    align-items: flex-end;
}
 .home112_text{
    color: rgb(195, 195, 195);
 }
 .home12{
    display: flex;
    height: 25vh;
    width: 40%;
    background-color: rgb(255, 255, 255);
    border-radius: 10px;
    justify-content: center;
    align-items: center;
}
.balance{
    color: rgb(195, 195, 195);
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 3em;
}
.balance_text{
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 1.3em;
    color: rgb(195, 195, 195);
}

.home2{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 5vh;
    padding: 2vh;
    padding-top: 4vh;
    padding-bottom: 4vh;
    background-color: rgb(236, 236, 236);
    border-radius: 10px;
}

.home_input{
    height: 3em;
    width: 100%;
    border: 0;
    background-color:rgba(143, 143, 143, 0.4);
    border-bottom: solid black 2px;
    border-radius: 10px;
    text-align: center;
    font-size: 1em
}

.home_btn{
    height: 8vh;
    width: 50%;
    background-color: var(--maincolor);
    color: white;
    border-radius: 10px;
    font-size: 1.5em;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-weight: 900;

}

.item{
    display: flex;
    width: 100%;
    justify-content: space-around;
    background-color: rgb(129, 129, 129);
    border-radius: 10px;
    gap: 1vh;
}
.item1{
    display: flex;
    width: 100%;
    justify-content: space-around;
    background-color: rgb(97, 97, 97);
    color: rgb(48, 48, 48);
    border-radius: 10px;
    gap: 1vh;
}

.item_box{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    padding: 20px;
    height: 69vh;
    /* overflow-x: scroll; */
    overflow-y: scroll;
    
}
.item_box::-webkit-scrollbar{
    display: none;
}

.item_date_box{
    display: flex;
    align-items: center;
    height: 3em;
    width: min-content;
    padding-left: 4px;
    padding-right: 2px;
}
.item_name_box{
    display: flex;
    align-items: center;
    justify-content:start;
    height: 3em;
    width: 100%;
    overflow-x: scroll;
    /* padding-left: 1vh;
    padding-right: 1vh; */
}
.item_name_box::-webkit-scrollbar{
    display: none;
}
.item_price_box{
    display: flex;
    align-items: center;
    height: 3em;
    width: 10vh;
}
.item_date{
    text-align: center;
}
.item_name{
    text-align:start;
}
.item_price{
    text-align: center;
}

.down{
    display: flex;
    position: fixed;
    height: 6.5vh;
    width: 6.5vh;
    background-color: var(--maincolor);
    z-index: 1;
    bottom: 11vh;
    right: 1vh;
    border-radius: 100%;
    justify-content: center;
    align-items: center;
}
.down_icon{
height: 1.5em;
width: 1.5em;
}
.pdf-box{
    display: flex;
    justify-content: center;
}
.download-pdf{
    align-items: center;
    font-size: 1.5vh;
    background-color: rgb(123, 123, 123);
    border-radius: 5px;
    padding: 0.6vh;

}
.pro-item-box{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    padding: 20px;
}
.pro-item{
    padding: 1vh;
    background-color: rgb(113, 113, 113);
    width: 100%;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 1.3em;
    font-weight: 600;
    border-radius: 10px;
}
.pro-item-box2{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    padding: 20px;
    background-color: rgb(236, 236, 236);
    border-radius: 10px;
}
.profile{
    display: flex;
    flex-direction: column;
    padding: 10px;
    height: 70vh;
    overflow-x:scroll;
}
.profile::-webkit-scrollbar{
    display: none;
}
.pdf{
border: solid black 1px;
width: 60vh;
text-align: center;
margin-left: 6vh;
margin-top: 10vh;
}

.pdf_gap{

    display: flex;
    flex-direction:column;
     gap: 40vh;
     padding-top: 0.5vh;
}
.print-pdf{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;

}